import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import { Divider } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import { IconButton } from '@material-ui/core';
import AddBoxOutlinedIcon from '@material-ui/icons/AddBoxOutlined';

const styles = {
  country: {
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: 10,
    padding: 10,
    width: 260,
  },
  divider: {
    marginTop: 5,
    marginBottom: 10,
  },
  countryName: {
    fontSize: '1.8em',
  },
  countryMedal: {
    marginTop: '5px',
    display: 'flex',
    alignItems: 'center',
  },
  goldAvatar: {
    color: 'black', 
    backgroundColor: '#ffc107',
  },
  medalName: {
    fontSize: '1.4em',
    paddingLeft: '10px',
    width: '130px',
  },
  addIconButton: {
    padding: '2px',
  }
}

class Country extends Component {
  state = {
    name: 'United States',
    goldMedalCount: 0,
  }
  handleIncrement = () => this.setState({ goldMedalCount: this.state.goldMedalCount + 1 });
  render() { 
    const { classes } = this.props;
    return (
      <Paper elevation={4} className={classes.country}>
        <div className={classes.countryName}>
          {this.state.name}
        </div>
        <Divider className={classes.divider} />
        <div className={classes.countryMedal}>
          <Avatar className={classes.goldAvatar}>
            {this.state.goldMedalCount}
          </Avatar>
          <span className={classes.medalName}>
            Gold Medals
          </span>
          <IconButton 
            onClick={this.handleIncrement}
            className={classes.addIconButton} 
            color='primary' 
            aria-label='increment gold'>
            <AddBoxOutlinedIcon fontSize='large' />
          </IconButton>
        </div>
      </Paper>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Country);
